import request from "@/utils/request"

const addUser = body => {
    return request('/system/user/add', { method: "POST", body })
}
const updateUser = body => {
  return request('/system/user/updateById', { method: "POST", body })
}
const getUser = body => {
  return request('/system/user/page', { method: "POST", body })
}
const registerUser = body => {
  return request('/system/user/register', { method: "POST", body })
} 
const updateUserStatus = body => {
  return request('/system/user/updateUserStatus', { method: "POST", body })
}
const getRioleList = body => {
  return request('/system/role/getList', { method: "POST", body })
}
const updateRole = body => {
  return request('/system/role/updateById', { method: "POST", body })
}
const deleteRole = body => {
  return request('/system/role/deleteById', { method: "POST", body })
}
const getLog = body => {
  return request('/system/user/loginLog/page', { method: "POST", body })
}

export default {
  addUser,
  updateUser,
  getUser,
  registerUser,
  updateUserStatus,
  getRioleList,
  updateRole,
  deleteRole,
  getLog
}
