<template>
  <div class="home">
    <div class="login">
      <el-form ref="formRef" :model="form" :rules="rules" style="width: 100%;" label-position="top">
        <el-form-item v-if="type === 'login'" label="账号/手机号" prop="user">
          <el-input v-model="form.user" clearable placeholder="请输入账号/手机号" />
        </el-form-item>
        <el-form-item v-if="type === 'login'" style="margin-bottom: 30px;" label="密码" prop="pass">
          <el-input v-model="form.pass" type="password" clearable placeholder="请输入密码" />
        </el-form-item>
        <el-form-item v-if="type !== 'login'" label="账号" prop="userName">
          <el-input v-model.trim="form.userName" clearable placeholder="请输入账号" />
        </el-form-item>
        <el-form-item v-if="type !== 'login'" style="margin-bottom: 30px;" label="密码" prop="password">
          <el-input v-model.trim="form.password" type="password" clearable placeholder="请输入密码" />
        </el-form-item>
        <el-form-item v-if="type !== 'login'" style="margin-bottom: 30px;" label="手机" prop="mobile">
          <el-input v-model.trim="form.mobile" clearable placeholder="请输入手机" />
        </el-form-item>
        <el-form-item v-if="type !== 'login'" style="margin-bottom: 30px;" label="邮箱" prop="email">
          <el-input v-model.trim="form.email" clearable placeholder="请输入邮箱" />
        </el-form-item>
        <el-button style="width: 100%; margin-bottom: 10px;" type="primary" @click="loginHandle">{{ type === 'login' ? '登录' : '注册' }}</el-button>
        <div class="registry">
          <el-link type="primary" @click="changeHandle"> {{ type === 'login' ? '注册账号' : '返回登录' }}</el-link>
        </div>
      </el-form>
    </div>
  </div>
</template>
<script>
import { reactive, ref } from 'vue'
import apis from '@/apis'
import {  useRouter} from 'vue-router'
import { ElMessage } from 'element-plus'
import { rules } from './config'
export default {
  setup() {
    const { registerUser } = apis
    const router = useRouter()
    const formRef = ref()
    const type = ref('login')
    const form = reactive({
      user: '',
      pass: '',
      userName: '',
      password: '',
      mobile: '',
      email: '',
    })
    const changeHandle = () => {
      type.value = type.value === 'login' ? 'registry' : 'login'
      formRef.value && formRef.value.resetFields()
    }
    const loginHandle = () => {
      formRef.value && formRef.value.validate((val) => {
        if (val) {
          if (type.value === 'login') {
            ElMessage.success('登录成功')
            router.push({  name: 'main'})
          } else {
            const {  user, pass, ...others} = form
            registerUser(others).then(res => {
              if (res?.success) {
                formRef.value && formRef.value.resetFields()
                ElMessage.success('注册成功')
                type.value = 'login'
              }
            })
            
          }
        }
      })
    }

    return {
      form,
      rules,
      formRef,
      type,
      changeHandle,
      loginHandle
    }
  },
}
</script>
<style lang="scss" scoped>
@import "./index.scss";
</style>
